import React from 'react';

class Auth extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="grey lighten-3">
                <div className="col s0 m3 l4 xl4" />
                <div className="white col s12 m6 l4 xl4">
                    {this.props.children}
                </div>
            </div>
        )
    }
}

export default Auth;


